.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 70%;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px grey;
    padding: 25px;
    left: 15%;
    top: 15%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    /* min-height: 40vh; */
    max-height: 500px;
    overflow-y: auto;
}

.ModalHeader{
    position: absolute;
    width: 70%;
    border-bottom: '1px solid #dcdcdc';
    padding-bottom: '.5em';
}

@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
        
    }
}